import React from 'react'
import styled from 'styled-components'
import clients from '../img/clients.jpg'
import { motion } from 'framer-motion'
import { useScroll } from '../resources/useScroll'
import { aboutTAnimation, aboutIAnimation } from '../resources/Animations'
import { mobile } from '../resources/Responsive'

const Container = styled.div`
    height: 90vh;
    background: #e1e1e1;
    display: flex;
    align-items: center;
    padding: 60px;
    overflow: hidden;
    ${mobile({
    display: 'grid',
    alignItems: 'center',
    padding: '40px'
})} 
`
const Text = styled(motion.div)`
    margin: 40px;
    text-align: center;
    ${mobile({
    width: '360px',
    alignItems: 'center',
    marginBottom: '-10px',
    marginLeft: '20px',
})} 
`
const H1 = styled.h1`
    font-size: 40px;
    ${mobile({
    fontSize: '25px',
    marginTop: '-60px',
})} 
`
const P = styled.p`
    font-size: 26px;
    padding: 30px;
    ${mobile({
    fontSize: '14px',
    padding: '0'
})}
`
const Image = styled(motion.img)`
    height: 450px;    
    width: 650px;
    border-radius: 10px;
    ${mobile({
    height: '300px',
    width: '400px'
})}
`
export default function About() {

    const [element, controls] = useScroll()

    return (
        <Container id='about' ref={element}>
            <Text variants={aboutTAnimation} animate={controls}>
                <H1>Quem Somos</H1>
                <P>
                    A seguradora Automotive tem o seguro ideal para o seu veículo.
                    Além de assistência de qualidade, você pode contar com
                    atendimento 24h e benefícios exclusivos. Não importa em que lugar
                    esteja, estaremos lá. Confira as melhores condições para
                    você renovar o seguro do seu veículo, seja ele carro, moto,
                    pick up ou caminhão.
                </P>
            </Text>
            <Image src={clients} variants={aboutIAnimation} animate={controls} />
        </Container>
    )
}
