import React from 'react'
import styled from 'styled-components'
import contacts from '../img/contacts.jpg'
import emailjs from '@emailjs/browser'
import { motion } from 'framer-motion'
import { useScroll } from '../resources/useScroll'
import { contactAnimation } from '../resources/Animations'
import { mobile } from '../resources/Responsive'

const Container = styled.div`
    position: relative;
    height: 63vh;
    background:url(${contacts});
    background-size: cover;
    background-position: center;
    overflow: hidden;
    ${mobile({
    height: "50vh"
})}
`
const Form = styled(motion.div)`
    background: rgba(0, 0, 0, 0.3);
    width: 450px;
    height: 360px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top:0;
    left: 0;
    right:0;
    bottom: 0;
    margin: auto;
    border-radius: 20px;   
    ${mobile({
    width: '350px',
    height: '280px'
})}
`
const Title = styled.div`
    color: white;
    margin-top: 10px;
    font-size: 16px;
    ${mobile({
    fontSize: '15px'
})}
`
const Input = styled.input`
    width: 380px;
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    font-size: 15px;
    ${mobile({
    width: '280px',
    height: '30px',
    fontSize: '10px'
})}
`
const TextArea = styled.textarea`
    width: 380px;
    height: 250px;
    padding: 10px;
    border-radius: 10px;
    margin: 10px;
    font-size: 15px;
    ${mobile({
    width: '280px',
    height: '100px',
    fontSize: '10px'
})}
`
const Button = styled.button`
    background: red;
    color: white;
    width: 100px;
    height: 60px;
    border: 20px;
    border-radius: 10px;
    cursor: pointer;
    margin-bottom: 10px;
    &:hover{
        background: darkred;
    }
    ${mobile({
    width: '70px',
    height: '25px'
})}
`
export default function Contact() {

    const [element, controls] = useScroll()

    function sendEmail(e) {
        e.preventDefault()

        emailjs.sendForm('YOUR_SERVICE_ID', 'YOUR_TEMPLATE_ID', e.target, 'YOUR_USER_ID'
        ).then((res) => {
            console.log(res)
        }).catch(err => console.log(err))
    }

    return (
        <Container id='contact' ref={element}>
            <Form onSubmit={sendEmail} variants={contactAnimation} animate={controls}>
                <Title>Entre em Contato</Title>
                <Input type='text' placeholder='Nome' />
                <Input type='email' placeholder='Email' />
                <TextArea placeholder='Escreva sua Menssagem' />
                <Button>Enviar</Button>
            </Form>
        </Container>
    )
}    
